<template>
  <div style="margin: auto">
    <h4 class="primary--text" style="padding-bottom: 20px">
      Use this form to inform others that there is new content available and where they can find it.
    </h4>
    <v-card class="messageForm" >
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-flex>
          <v-text-field
            label="URL"
            v-model="url"
            required
            :rules="[v => !!v || 'URL is required']"
            placeholder="Enter a valid URL (required)"
          ></v-text-field>
        </v-flex>
          <v-select
            label="Select data type"
            :items="items"
            v-model="dataType"
            required
            :rules="[v => !!v || 'Data type is required']"
            autocomplete
          >
          </v-select>
        <v-text-field
          label="Title"
          v-model="title"
          required
          :rules="[v => !!v || 'Title is required']"
          placeholder="Enter a title (required)"
        ></v-text-field>
        <v-text-field
          label="Description"
          v-model="description"
          placeholder="Enter a description (optional)"
        ></v-text-field>
      </v-form>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          @click="submit"
          :disabled="!valid"
        >
          submit
        </v-btn>
        <v-btn @click="clear">clear</v-btn>
      </v-card-actions>
      <v-snackbar
        :top="true"
        v-model="snackbarSuccess"
        class="snackbarSuccess"
      >
        {{snackbarSuccessText}}
        <v-btn flat color="white" @click.native="snackbarSuccess = false">Close</v-btn>
      </v-snackbar>
      <v-snackbar
        :top="true"
        v-model="snackbarError"
        class="snackbarError"
      >
        {{snackbarErrorText}}
        <v-btn flat color="white" @click.native="snackbarError = false">Close</v-btn>
      </v-snackbar>
    </v-card>

  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    name: 'MessageForm',
    data() {
      return {
        snackbarSuccess: false,
        snackbarError: false,
        snackbarSuccessText: '',
        snackbarErrorText: '',
        valid: true,
        url: '',
        title: '',
        dataType: '',
        items: [
          {text: 'bmp', value: 'image_bmp'},
          {text: 'gif', value: 'image_gif'},
          {text: 'geotiff', value: 'image_geotiff'},
          {text: 'jpeg', value: 'image_jpeg'},
          {text: 'png', value: 'image_png'},
          {text: 'audio_mpeg', value: 'audio_mpeg'},
          {text: 'video_mpeg', value: 'video_mpeg'},
          {text: 'msvideo', value: 'video_msvideo'},
          {text: 'avi', value: 'video_avi'},
          {text: 'msword', value: 'msword'},
          {text: 'pdf', value: 'pdf'},
          {text: 'excel', value: 'excel'},
          {text: 'powerpoint', value: 'powerpoint'},
          {text: 'zip', value: 'zip'},
          {text: 'other', value: 'other'},
          {text: 'json', value: 'json'},
          {text: 'geoJson', value: 'geojson'},
          {text: 'text_plain', value: 'text_plain'},
          {text: 'vorbis', value: 'audio_vorbis'},
          {text: 'ogg', value: 'ogg'},

        ],
        description: ''
      }
    },
    methods: {
      submit () {
        const self = this;
        if (this.$refs.form.validate()) {
          axios.post('http://localhost:8190/CISRestAdaptor/sendLargeDataUpdateJson', {
            url: this.url,
            dataType: this.dataType,
            title: this.title,
            description: this.description
          }).then(response => {
            self.snackbarSuccess = true;
            self.snackbarSuccessText = 'Data was successfully submitted.'
            self.clear()
          })
            .catch(e => {
              self.snackbarError = true;
              self.snackbarErrorText = 'Data was not successfully submitted.'
            });
        }
      },
      clear () {
        this.$refs.form.reset()
        this.valid = true
      }
    }
  }
</script>

<style scoped>
  .label {
    display: block !important
  }
</style>




